import React, { Component } from 'react'
import comb from './index.module.css'

export default class Comb extends Component {

  render() {
    console.log(this.props);
    const { imgsrc } = this.props;
    console.log(imgsrc)
    return (
      <div className={comb.bus_show}>
          <div className={comb.show_box}>
            {
              imgsrc.map((list, index)=>{
                return (
                  <div key={ index } className={comb.row}>
                    {
                      list.map((item, idx)=>{
                        return (
                          <div key={item.id} className={comb.hex}>
                            <span>
                              <img src={item.src} alt={item.titl} />
                            </span>
                          </div>
                        )
                      })
                    }
                  </div>
                )
              })
            }
          </div>
      </div>
    )
  }
}
