import React, { useRef, useEffect, useState } from 'react';
import { nanoid } from 'nanoid';

import ImgBox from '../components/ImgBox';

import img from '../asset/img/soup1.png'

export default function Patent() {

    const patentRef = useRef();
    const [height, setHeight] = useState(0);

    const patents = [
        {id: nanoid(), patentImg: img, patentName: '1', patentProfile: ' Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequuntur, voluptas.'},
        {id: nanoid(), patentImg: img, patentName: '2', patentProfile: ' Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequuntur, voluptas.'},
        {id: nanoid(), patentImg: img, patentName: '3', patentProfile: ' Lorem ipsum dolor sit, amet consectetur adipisicing elit. Ducimus, soluta fuga!'},
        {id: nanoid(), patentImg: img, patentName: '4', patentProfile: ' Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequuntur, voluptas.'},
        {id: nanoid(), patentImg: img, patentName: '5', patentProfile: ' Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequuntur, voluptas.'},
        {id: nanoid(), patentImg: img, patentName: '6', patentProfile: ' Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequuntur, voluptas.'}
    ]
    
    useEffect(()=>{
        setHeight(patentRef.current.scrollHeight * 4);
    },[]);

  return (
    <ul className='patentlist' style={{maxHeight: height}}>
        {
            patents.map((patent, index)=>(
                <li key={patent.id} ref={patentRef}>
                    <ImgBox imgsrc={patent.patentImg} width='100%' height='50%' />
                    <div className="patentInfo">
                        <h3>{patent.patentName}</h3>
                        <p>{patent.patentProfile}</p>
                    </div>
                </li>
            ))
        }
    </ul>
  )
}
