import React, { Component } from 'react'

import article from './index.module.css'

export default class ArticleTitle extends Component {
    render() {
        const { value, orderNum, style } = this.props;
        const containerStyle = style === undefined ? '' : {
            fontSize: style.fontSize === undefined ? '1.5rem' : style.fontSize,
            marginTop: style.marginTop === undefined ? '4rem' : style.marginTop,
            marginBottom: style.marginBottom === undefined ? '2rem' : style.marginBottom,
        }
        return (
            <div className={article.articleTitle} style={{...containerStyle}}>
                <p>{orderNum}</p>
                <h3>{value}</h3>
            </div>
        )
    }
}
