import React from 'react'

import iconStyle from './index.module.css'

export function ArrowDownB(props) {
  const { color } = props;
  return (
    <div className={ iconStyle.arrow_down_b } style={{borderColor: color === undefined ? '' : color}}>
      <span className={`${iconStyle.arrow_b} ${iconStyle.a_l_b}`} style={{borderColor: color === undefined ? '' : color}}></span>
      <span className={`${iconStyle.arrow_b} ${iconStyle.a_m_b}`} style={{backgroundColor: color === undefined ? '' : color}}></span>
      <span className={`${iconStyle.arrow_b} ${iconStyle.a_r_b}`} style={{borderColor: color === undefined ? '' : color}}></span>
    </div>
  )
}


export function ArrowDownS(props){
  const { color } = props;
  return (
    <div className={ iconStyle.arrow_down_s }>
      <span className={`${iconStyle.arrow_s} ${iconStyle.a_m_s}`} style={{borderColor: color === undefined ? '' : color}}></span>
      <span className={`${iconStyle.arrow_s} ${iconStyle.a_r_s}`} style={{backgroundColor: color === undefined ? '' : color}}></span>
      <span className={`${iconStyle.arrow_s} ${iconStyle.a_l_s}`} style={{borderColor: color === undefined ? '' : color}}></span>
    </div>
  )
}

const arrow_comp = {
  ArrowDownB, ArrowDownS
}

export default arrow_comp