import React, { Component } from 'react'
import AMapLoader from '@amap/amap-jsapi-loader';

import mapStyle from './index.module.css'

export default class MapContainer extends Component {

    componentDidMount(){
        AMapLoader.load({
            "key": "9628dbf6c0bfbdf7a359581c7f686e6e",
            "version": "2.0",
            "plugins": [],
        }).then((AMap)=>{
            const map = new AMap.Map('mapContainer', {
                zoomEnable: false,
                dragEnable: false,
                touchZoom: false,
                zoom: 12,
            });
            console.log(map);
        }).catch(e => {
            console.log(e);
        })
    }
    render() {
        return (
            <div className={mapStyle.screen}>
                <div id="mapContainer" className={mapStyle.gdMap} />
            </div>
        )
    }
}
