import { Navigate } from 'react-router-dom';

import Health from '../pages/Health';
import Inherit from '../pages/Inherit';
import Source from '../pages/Source';
import Hmong from '../pages/Hmong';
import Mountain from '../pages/Mountain';
import About from '../pages/About';
import Expert from '../pages/Expert';
import Patent from '../pages/Patent';
import University from '../pages/University';

const routes = [
    {
        path: '/hmong',
        element: <Hmong />
    },
    {
        path: '/health',
        element: <Health />
    },
    {
        path: '/inherit',
        element: <Inherit />
    },
    {
        path: '/source',
        element: <Source />
    },
    {
        path: '/mountain',
        element: <Mountain />
    },
    {
        path: '/about',
        element: <About />,
        children: [
            {
                path: 'expert',
                element: <Expert />
            },
            {
                path: 'patent',
                element: <Patent />
            },
            {
                path: 'university',
                element: <University />
            },
            {
                path: '',
                element: <Navigate to='/about/expert' />,
            }
        ]
    },
    {
        path: '/',
        element: <Navigate to='/hmong' />
    }
]

export default routes