import React, { Fragment } from 'react'

import ImgBox from '../components/ImgBox';
import Head from '../components/Head';
import PageTitle from '../components/PageTitle';
import ArticleTitle from '../components/ArticleTitle';
import Slide from '../components/Slide';
import slideImgs from './srcList/slideImgs';

import { useWinSize } from '../utils/useWinSize'

export default function Health() {
    const size = useWinSize();
    return (
        <Fragment>
            <div id="p4">
                <Head />
                <PageTitle titl1="" titl2="" imgsrc={require('../asset/img/health_figure.png')} />
                <div className='m-body'>
                    <ArticleTitle value='&nbsp;' />
                    <div className='articlecontent'>
                        <ImgBox imgsrc={require('../asset/img/medicine.png')} width={'95%'} height={'135%'} boxMargin='1rem auto' />
                        <p></p>
                    </div>
                    <Slide {...slideImgs} size={size}/>
                    <ArticleTitle value='&nbsp;' />
                    <div className='articlecontent'>
                        <ImgBox imgsrc={require('../asset/img/soup1.png')} width={'95%'} height={'135%'} boxMargin='1rem auto'/>
                        <ImgBox imgsrc={require('../asset/img/soup2.png')} width={'95%'} height={'45%'} boxMargin='1rem auto'/>
                        <ImgBox imgsrc={require('../asset/img/soup3.png')} width={'95%'} height={'45%'} boxMargin='1rem auto'/>
                        <p><b></b></p>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
