import React,{ Fragment, useState, useEffect, useRef } from 'react';
import { nanoid } from 'nanoid';

export default function University() {

    const [height, setHeight] = useState();
    const uniRef = useRef();

    const colleges = [
        {id: nanoid(), collegeName: 'X University'},
        {id: nanoid(), collegeName: 'XXX University'},
        {id: nanoid(), collegeName: 'XXXXXX University'},
        {id: nanoid(), collegeName: 'XX University'},
        {id: nanoid(), collegeName: 'XXXX University'},
        {id: nanoid(), collegeName: 'XX University'},
        {id: nanoid(), collegeName: 'XXXXX University'},
        {id: nanoid(), collegeName: 'XXX University'}
    ];

    useEffect(()=>{
        setHeight(uniRef.current.scrollHeight * 6);
        console.log(height);
    }, []);

  return (
    <Fragment>
        <section className="uniShow">
            <div><span><img src="#" alt="" /></span></div>
            <div><span><img src="#" alt="" /></span></div>
            <div><span><img src="#" alt="" /></span></div>
            <div><span><img src="#" alt="" /></span></div>
        </section>
        <section className="uniShow">
            <div><span><img src="#" alt="" /></span></div>
            <div><span><img src="#" alt="" /></span></div>
            <div><span><img src="#" alt="" /></span></div>
            <div><span><img src="#" alt="" /></span></div>
        </section>
        <ul className="unilist" style={{maxHeight: height}}>
            {
                colleges.map((college, index)=>(
                    <li key={college.id} ref={uniRef}>
                        <p>{college.collegeName}</p>
                    </li>
                ))
            }
        </ul>
    </Fragment>
  )
}
