import React, { Component } from 'react'

import Introsec from './index.module.css'

export default class IntroSec extends Component {
  render() {
    const { titl, contnt } = this.props;
    return (
        <div className={Introsec.sec}>
            <div className={Introsec.subtitl}>{titl}</div>
            <p>{ contnt }</p>
        </div>
    )
  }
}

