import React from 'react'
import { NavLink, useRoutes } from 'react-router-dom'
import routes from './routes'
import './App.css'

export default function App() {
  const element = useRoutes(routes)
  return (
    <div>
      <div className='hide'>
        <NavLink to='/inherit'>chuancheng </NavLink>
        <NavLink to='/source'>ziyuan </NavLink>
        <NavLink to='/health'>yangsheng </NavLink>
        <NavLink to='/hmong'>miaozuren </NavLink>
      </div>
      { element }
    </div>
  )
}


