import React, { Fragment }  from 'react'
import { NavLink } from 'react-router-dom';

import Head from '../components/Head';
import PageTitle from '../components/PageTitle';
import ArticleTitle from '../components/ArticleTitle';
import ImgBox from '../components/ImgBox'

import mountainImgs from './srcList/mountainImgs';

export default function Source() {

    const { items:mountains } = mountainImgs;

    return (
        <Fragment>
            <div id="p3">
                <Head />
                <PageTitle titl1="" titl2="" imgsrc={require('../asset/img/mountain_figure.png')} />
                <div className='m-body'>
                    <ArticleTitle value='' />
                    <div className='articlecontent'>
                        {
                            mountains.map((item, index) => (
                                <NavLink key={item.id} to='/mountain' state={{...item}}>
                                    <ImgBox 
                                        imgsrc={item.imgsrc} 
                                        imgdesc={item.imgdesc} 
                                        width={'95%'} height={'45%'} 
                                        boxMargin='1rem auto' 
                                    />
                                </NavLink>
                            ))
                        }
                    </div>
                    <ArticleTitle value='' />
                    <div className='articlecontent'>
                        <ImgBox imgsrc='#' width={'95%'} height={'135%'} boxMargin='1rem auto' />
                        <ImgBox imgsrc='#' width={'95%'} height={'135%'} boxMargin='1rem auto' />
                        <ImgBox imgsrc='#' width={'95%'} height={'135%'} boxMargin='1rem auto' />
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
