import React, { Fragment } from 'react'
import { useLocation } from 'react-router-dom'

import { ArrowDownS } from '../components/Arrow';
import ImgBox from '../components/ImgBox';

export default function Mountain() {
    const { state:{imgsrc2, imgdesc, loc, intro} } = useLocation();
    return (
        <Fragment>
            <div className="nav">
                <h2 className="title"></h2>
            </div>
            <div className="pagetitle" id='mountain'>
                <ImgBox imgsrc={imgsrc2} className='pos-ab'/>
                <div className="imgdesc">
                    <h1>{ imgdesc }</h1>
                    <p dangerouslySetInnerHTML={{__html: loc}}></p>
                </div>
                <div className="icon-box">
                    <ArrowDownS />
                </div>
            </div>
            <div className="content">
                <div className="identity">
                    <p dangerouslySetInnerHTML={{__html: intro}} />
                </div>
                <ImgBox width={'92%'} height={'135%'} imgsrc='#' boxMargin='1rem auto' />
                <div className='articlecontent'>
                    <p>1386.5<b></b></p>
                </div>
                <ImgBox width={'92%'} height={'135%'} imgsrc='#' boxMargin='1rem auto' />
                <div className="articlecontent">
                    <p><b></b>400300100<b></b></p>
                </div>
            </div>
        </Fragment>
    )
}
