import React, { Component } from 'react'

import ImgBox from '../ImgBox'

import itemStyle from './index.module.css'

export default class SlideItem extends Component {
    render() {
        const { imgsrc, imgdesc , size} = this.props;
        return (
            <div className={itemStyle.item} style={{...size}}>
                <ImgBox imgsrc={imgsrc} width='100%' height='100%' boxMargin='0 auto' boxBorder='0'/>
                <label>{ imgdesc }</label>
            </div>
        )
    }
}
