import React, { Component  } from 'react';
import { createRef } from 'react';

import SlideItem from '../SlideItem';

import slideStyle from './index.module.css';

export default class Slide extends Component {

    state = {
        scrollLeft: 0,
        scrollWidth: 0
    }

    stageRef = createRef();

    static getDerivedStateFromProps(nextProps, prevState){
        const { size } = nextProps;
        if( size !== prevState.size){
            const newWidth = size.width;
            return {scrollWidth: newWidth};
        }
        return null;
    }

    handleScroll = (event)=>{
        const { scrollLeft} = event.target;
        this.setState({scrollLeft});
    }

    render() {
        const { items, size } = this.props;
        const width = (size.width - 20) / 3;
        const slideLength = items.length * width;
        const itemSize = {width: width-10, height: width-10};
        const { scrollLeft, scrollWidth } = this.state;
        const scrollBarLeft = scrollLeft * 0.25, scrollLength = (size.width - 20) * 0.25;
        console.log('scrollLeft', scrollLeft, '', scrollLength, '', slideLength )
        
        return (
            <div className={slideStyle.slideStage}>
                <div className={slideStyle.stage} onScroll={this.handleScroll} ref={this.stageRef}>
                    <div className={slideStyle.slide} style={{width: slideLength}}>
                        {
                            items.map((item, index) => {
                                return <SlideItem key={item.id} imgsrc={item.imgsrc} imgdesc={item.imgdesc} size={itemSize} />
                            })
                        }
                    </div>
                </div>
                <div className={slideStyle.scrollBar} style={{width: (scrollWidth - 20) * 0.25 + scrollLength }}>
                    <div className={slideStyle.inner} style={{left: scrollBarLeft, width: scrollLength}}></div>
                </div>
            </div>
        )
    }
}
