import React, { Component } from 'react'

import imgbox from './index.module.css'

export default class ImgBox extends Component {
  render() {
    const {width, height, imgsrc, imgdesc, boxMargin, boxBorder, className} = this.props;
    const html = imgdesc === undefined ? '' : imgdesc;
    return (
        <span className={className === undefined ? imgbox.imgbox : className} style={{width, paddingTop: height, margin: boxMargin, borderRadius: boxBorder}}>
            <img src={imgsrc} alt="" />
            <label>{html}</label>
        </span>
    )
  }
}
